.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
  .load_spin {
    animation: App-logo-spin infinite 2s ease-out;
  }
}

.App-header {
  background-color: #dcdde1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Compose */
.compose_container {
  background: white;
  color: grey;
  border-top: solid 15px rgb(69, 193, 150);
  padding: 20px;
  border-radius: 15px;
  box-shadow: 2px 2px 3px rgba(128, 128, 128, 0.406);
}

.compose_section {
  display: flex;
  justify-content: space-around;
}

.compose_section > * {
  margin: 5px;
}

.compose_in {
  border-radius: 30px;
  border-style: none;
  background:#dcdde1;
  padding: 10px;
  font-size: 16px;
  min-width: 70%;
  text-align: left;
  color: black;
  padding-top: 15px;
}

.compose_in:focus {
  outline: none;
  box-shadow: 0 0 1px 1px rgb(69, 193, 150);
}

.compose_in:empty::before {
  content: "What's on your mind?";
  color: grey;
  transition: width 2s;
}

.image_circle {
  background: rgb(161, 161, 201);
  border-radius: 50px;
  height: 50px;
  width: 50px;
  overflow: hidden;
}

.inner_image {
  background: rgb(179, 79, 79);
  border-radius: 50px;
  height: 25px;
  width: 25px;
  position: relative;
  left: 12px;
  top: 6px;
}

.inner_image2 {
  background: rgb(225, 187, 187);
  border-radius: 50px;
  height: 25px;
  width: 25px;
  position: relative;
  left: 12px;
  top: 6px;
}

/* Post Box */

.postBox_container {
  margin: 20px;
  min-width: 500px;
  max-width: 500px;

}

.break_out {
  border-top: 1px solid rgb(174, 174, 174);
}

.postBox_container > * {
  margin: 20px;
}

/* Post */
.post_container {
  background: white;
  color: grey;
  border-top: solid 15px rgb(69, 193, 150);
  padding: 5px;
  border-radius: 15px;
  box-shadow: 2px 2px 3px rgba(128, 128, 128, 0.406);
}

.profile_section {
  display: flex;
  justify-content: baseline;
  position: relative;
  padding-left: 20px;
}

.break {
  border-top: 1px solid rgb(220, 220, 220);
  width: 100%;
}

.profile_section > * {
  margin: 5px;
}

.username_post {
  line-height: 10px;
  font-weight: 600;
}

.body_section {
  text-align: left;
  padding: 20px;
  display: flex;
  justify-content: space-between;
}

/* NavBar */
.navbar_container {
  background: white;
  display: flex;
  justify-content: center;
  padding: 20px;
  color: grey;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 2px 2px 3px rgba(128, 128, 128, 0.406);
}

.navbar_wrapper {
  width: 70%;
  display: flex;
  justify-content: space-around;
}

.navbar_wrapper > a {
  color: inherit;
  text-decoration: none;
  transition: .3s ease;
  
}

.navbar_wrapper > a:hover {
  color: black;
}

/* Forms */
.form_container {
  background: white;
  color: grey;
  padding: 5px;
  border-radius: 15px;
  box-shadow: 2px 2px 3px rgba(128, 128, 128, 0.406);
  padding: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  transition: ease .5s;
}

.form_container:hover {
  box-shadow: 0 0 1px 10px rgb(69, 193, 150);
  
}

.form_wrapper {
  width: 20ch;
}

.input_field {
  display: flex;
  padding-top: 10px;
  flex-direction: column;
  justify-content: space-around;
}

.input_field > input {
  border-radius: 30px;
  border-style: none;
  background:#dcdde1;
  padding: 10px;
  font-size: 16px;
  min-width: 20%;
  text-align: left;
  color: black;
  padding-top: 15px;
}

.input_field > label {
  font-size: 20px;
}

.input_field > input:focus {
  outline: none;
  box-shadow: 0 0 1px 1px rgb(69, 193, 150);
}

.submit_button {
  border-radius: 15px;
  padding: 10px;
  border-style: none;
  background:#ffffff;
  box-shadow: .2px .2px 5px rgba(128, 128, 128, 0.406);
  transition: .3s ease;
  margin-top: 20px;
}

.submit_button:hover {
  background: rgb(69, 193, 150);
  padding: 10px;
  cursor: pointer;
  transform: scale(1.2);
}

/* Footer */
.footer {
  padding: 20px;
  height: 100%;
  
  background: rgb(106, 113, 120);
}